body {
  width: 100%;
  height: 100vh;
  font-size: 11.5px;
  font-weight: bold;
  color: rgb(80, 80, 80);
  align-self: flex-end;
}

.containerFooter {
  background-color: #f5f5f5 !important;
  padding: 0;
  width: 100%;
}

.card {
  padding: 2% 7%;
  color: #646771;
  background-color: #f5f5f5 !important;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

ul > li {
  padding: 4px;
}

ul > li:hover {
  color: #957bda;
  cursor: pointer;
}

hr {
  border-width: 3px;
}

.social > i {
  padding: 1%;
  font-size: 15px;
}

.social > i:hover {
  color: #957bda;
  cursor: pointer;
}

.policy > div {
  padding: 4px;
}

.heading {
  font-family: "Titillium Web", sans-serif;
  color: white;
}

.divider {
  border-top: 2px solid rgba(189, 196, 203, 0.5);
}
